* {
  font-family: Urbanist, sans-serif;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.header {
  height: 15%;
  width: 100%;
  min-height: 80px;
  background: linear-gradient(133deg, #ffd1ff 0%, #fcead5 49%, #fbd0c8 100%);
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.header__pic {
  align-items: center;
  display: flex;
}

.header__menu {
  width: 500px;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 45px;
  list-style: none;
  display: flex;
}

.header__item:hover {
  text-decoration: underline;
}

.header__icon {
  width: 100px;
  flex-direction: row;
  justify-content: space-between;
  margin-right: 20px;
  display: flex;
}

body {
  flex-flow: wrap;
  display: flex;
}

.filter {
  flex-flow: column wrap;
  margin: 50px;
  display: flex;
}

.filter__btn {
  background: none;
  border: none;
  flex-direction: column;
  font-size: 28px;
  text-decoration: none;
  display: flex;
}

.filter__pName {
  flex-wrap: wrap;
  display: flex;
}

